<template>
    <div class="avatar-section">
        <div class="avatar-icon" v-if="photoURL">
                <figure class="image">
            <router-link to="/mypage" >
                    <img class="is-rounded" :src="photoURL">                                
            </router-link>
                </figure>
        </div>
        <div class="avatar-icon" v-else>
                <figure class="image">
            <router-link to="/">
                    <img class="is-rounded"><i class="fas fa-user"></i>                                
            </router-link>
                </figure>
        </div>
    </div>
</template>

<script>
import { 
    getAuth, 
    onAuthStateChanged, 
} from "firebase/auth";


export default {
    name: "Avatar",
    data() {
        return {
            photoURL: "",
        }
    },
    methods: {
        },
    mounted() {
        const auth = getAuth()
        onAuthStateChanged(auth, (user) => {
            if (user) {
                this.photoURL = user.photoURL
            }
        })
    },
    computed() {
    },
}
</script>