<script>
import { 
    getAuth, 
    onAuthStateChanged, 
} from "firebase/auth";

// import compoenents
import Header from "./components/Header.vue";
import Footer from "./components/Footer.vue";

export default {
  name: 'App',
  components: {
    Header,
    Footer,
  },
  methods: {
    
  },
  created() {
    const auth = getAuth()
    onAuthStateChanged(auth, (user) => {
      if (user) {
        //User is signed in.
        this.$store.commit("setAuthentication")
        console.log("isAuthenticated", this.$store.state.isAuthenticated)
      } else {
        // user not logged in.
      }
    })

  },
  mounted() {
  },
}
</script>

