<script>
import { 
    getAuth, 
    onAuthStateChanged, 
} from "firebase/auth";
import {
  getFirestore, 
  collection,
  doc,
  setDoc,
  getDoc,
  getDocs,
} from "firebase/firestore";

export default {
  name: 'MyPage',
  data() {
    return {
      userName: '',
      userEmail: '',
      userClassName: '',
      userClassStartdate: '',
      planList: {
        basic: {
          planName: '',
          planPrice: 0,
        },
        standard: {
          planName: '',
          planPrice: 0,
        },
        premium: {
          planName: '',
          planPrice: 0,
        }
      },
      membershipColor: '',
      membershipTextColor: '',
    }
  },
  methods: {
    saveChagnes() {
      // need 
      // to 
      // make 
      // this
      // method
    },

    upgradeToPremium() {
      const auth = getAuth()
      const premiumClassObj = {
        className: "premium",
        startDate: new Date()
      }
      onAuthStateChanged(auth, (user) => {
        if (user) {
          const uuid = user.uid
          const db = getFirestore()
          const usersRef = collection(db, "users")
          const docRef = doc(usersRef, uuid)
          getDoc(docRef)
            .then(docSnap => {
              if (docSnap.exists()) {
                const classList = docSnap.data()['class']
                if (classList) {
                  const currentClassObj = classList[classList.length - 1]
                  const currentClassName = currentClassObj['className']
                  if (currentClassName !== 'premium') {
                    classList.push(premiumClassObj)
                    setDoc(doc(usersRef, uuid), {class: classList}, { merge: true })
                    this.$toast.success("Upgrade Completed.")
                  } else {
                    this.$toast.error("You're already Premium.")
                  }
                }
              }
            })
        }
      })
    },

    upgradeToStandard() {
      const auth = getAuth()
      const standardClassObj = {
        className: "standard",
        startDate: new Date()
      }
      onAuthStateChanged(auth, (user) => {
        if (user) {
          const uuid = user.uid
          const db = getFirestore()
          const usersRef = collection(db, "users")
          const docRef = doc(usersRef, uuid)
          getDoc(docRef)
            .then(docSnap => {
              if (docSnap.exists()) {
                const classList = docSnap.data()['class']
                if (classList) {
                  const currentClassObj = classList[classList.length - 1]
                  const currentClassName = currentClassObj['className']
                  if (currentClassName === 'basic') {
                    classList.push(standardClassObj)
                    setDoc(doc(usersRef, uuid), {class: classList}, { merge: true })
                    this.$toast.success("Upgrade Completed.")
                  } else if (currentClassName === 'standard') {
                    this.$toast.error("You're already Standard.")
                  } else if (currentClassName === 'premium') {
                    this.$toast.error("You're already Premium.")
                  } 
                }
              }
            })
        }
      })
    }
  },
  mounted() {
    const auth = getAuth()

    onAuthStateChanged(auth, (user) => {
      this.userName = user.displayName
      this.userEmail = user.email
      const uuid = user.uid
      const db = getFirestore()
      const usersRef = collection(db, "users")
      const docRef = doc(usersRef, uuid)
      getDoc(docRef)
        .then(docSnap => {
          if (docSnap.exists()) {
            const classList = docSnap.data()['class']
            if (classList) {
              const currentClassObj = classList[classList.length - 1]
              this.userClassName = currentClassObj['className']
              this.userClassStartdate = currentClassObj['startDate']
              // define membershipColor
              if (this.userClassName === 'basic') {
                this.membershipColor = 'is-info'
                this.membershipTextColor = 'has-text-info'
              } else if (this.userClassName === 'standard') {
                this.membershipColor = 'is-warning'
                this.membershipTextColor = 'has-text-warning-dark'
              } else if (this.userClassName === 'premium') {
                this.membershipColor = 'is-danger'
                this.membershipTextColor = 'has-text-danger'
              }
            }
          }
        })
        .catch((err) => {
          console.log(err)
        })
      
      // plan list
      const paymentPlan = collection(db, "paymentPlan")
      const planRef = doc(paymentPlan, "i0n63amqxAf6pzKqrvsD")
      getDoc(planRef)
        .then(docSnap => {
          if (docSnap.exists()) {
            const planList = docSnap.data()['plans']
            if (planList) {
              this.planList = planList
            }
          }
        })
        .catch((err) => {
          console.log(err)
        })
    })

  },

}
</script>

