import { 
    getAuth, 
    onAuthStateChanged, 
    signInWithRedirect, 
    signInWithPopup, 
    GoogleAuthProvider,
    signOut, 
} from "firebase/auth";
import {
    getFirestore, 
    collection,
    doc,
    setDoc,
    getDoc,
    getDocs,
  } from "firebase/firestore";

const initObj = {
    init: true,
    class: [
        {
            className: "basic",
            startDate: new Date()
        }
    ],
    nickname: "",
    profileImageUrl: "",
    team: "",
    role: "",
}

const methods = {
    loginWithGoogle() {
        console.log("Login with google.")

        const auth = getAuth()
        const googleProvider = new GoogleAuthProvider()

        signInWithPopup(auth, googleProvider)
            .then((result) => {
                const credential = GoogleAuthProvider.credentialFromResult(result);
                
                const user = result.user;
                const accessToken =  credential.accessToken; // credential.idToken;
            
                this.$store.commit("setAuthentication")
                
                // User Initiation
                console.log("User initiate.")
                const uuid = user.uid
                const db = getFirestore()
                const usersRef = collection(db, "users")
                const docRef = doc(usersRef, uuid)
                getDoc(docRef)
                    .then(docSnap => {
                    if (docSnap.exists()) {
                        if (!docSnap.data()['init']) {
                            setDoc(doc(usersRef, uuid), initObj)
                            console.log("user initiated.")
                        } else {
                            console.log("user already initiated.")
                        }
                    } else {
                        setDoc(doc(usersRef, uuid), initObj)
                        console.log("user initiated.")
                    }
                    })
                    .catch((err) => {
                        console.log(err)
                    })

            })
            .catch((error) => {
                console.log(error)
                this.$toast.error("Login failed. Please try again.")
            });
    },

    logoutFromGoogle() {
        console.log("logout from google.")

        const auth = getAuth()
        signOut(auth)
            .then((res) => {
                this.$store.commit("removeAuthentication")
                this.$router.push('/')
            })
            .catch((err) => {
                console.log(err)
            })
    },
}

export default {
    install(Vue) {
        Vue.config.globalProperties.$loginWithGoogle = methods.loginWithGoogle;
        Vue.config.globalProperties.$logoutFromGoogle = methods.logoutFromGoogle;
        Vue.config.globalProperties.$userInit = methods.userInit;
    }
}