<template>
    <div class="uploadfile-page">
        <button class="button is-link is-focused is-fullwidth" :class="{'is-hidden': this.$store.state.isAuthenticated}" @click="$loginWithGoogle"> Upload </button>
        <div class="file is-boxed has-name is-fullwidth" :class="{'is-hidden': !this.$store.state.isAuthenticated}">
            <label class="file-label" @click="checkAuth">
                <input class="file-input" type="file" accept=".txt" name="resume" ref="myFiles" @change="previewFiles" multiple>
                <span class="file-cta">
                <span class="file-icon">
                    <i class="fas fa-upload"></i>
                </span>
                <span class="file-label">
                    Choose a file...
                </span>
                </span>
                <p class="file-name has-background-white-ter">
                    <span class="has-text-dark" v-if="filelist.length === 1">
                        {{ rep_filename }} 
                    </span>
                    <span class="has-text-dark" v-if="filelist.length > 1">
                        {{ rep_filename }} ...
                    </span>
                </p>
                <button class="button is-link" :class="{'is-loading': isUploading}" @click="uploadFiles" v-if="filelist.length === 0"> Upload </button>
                <button class="button is-link" :class="{'is-loading': isUploading}" @click="uploadFiles" v-else> Upload {{ filelist.length }} files </button>
                <button class="button is-danger" :class="{'is-hidden': (this.filelist.length === 0)|(isUploading)}" @click="this.filelist=[]"> 
                    <span>Delete</span>
                    <span class="icon is-small">
                    <i class="fas fa-times"></i>
                    </span> 
                </button>
            </label>
        </div>
    </div>
</template>

<script>
import { getStorage, ref, uploadBytes, listAll } from "firebase/storage";
import { getAuth } from "firebase/auth";

export default {
    name: "FileUploader",
    data() {
        return {
            filelist: [],
            rep_filename: '',
            isUploading: false
        }
    },
    methods: {
        previewFiles(e) {
            if (e.target.files[0]) { // if e.target.files exists,
                this.filelist = e.target.files
                this.rep_filename = this.filelist[0].name;
            }
        },
        async uploadFiles(e) {
            // check if there is filelist to be uploaded.
            if (this.filelist.length === 0 ) {
                this.$toast.error("Choose files first.")
            } else {
                const storage = getStorage();
                const uuid = getAuth().currentUser.uid;
                
                const endUploadSuccess = () => {
                    this.filelist = []
                    this.isUploading = false 
                    this.$toast.success('Successfully uploaded.')
                }
    
                const endUploadFail = () => {
                    this.isUploading = false 
                    this.$toast.error('Error occured during uploading.')
                }
    
                let hasError = false;
                
                // 경로에 있는 파일 이름 읽고 겹치는 이름 있는지 파악해서 처리해주는 기능 필요
                const listRef = ref(storage, `scrim_log/${uuid}/`)
                listAll(listRef)
                    .then((res) => {
                        items = res.items
                        console.log(items)
                    })
                    .catch((err) => {

                    })

                // Upload to firebase storage
                for (let file of this.filelist) {
                    // get date
                    const date = new Date();
                    const currentTime = date.toLocaleString('en-US').replace(/[\/\,]/gi, " ").replace("  ", " ")

                    this.isUploading = true
                    const scrimLogRef = ref(storage, `scrim_log/${uuid}/(${currentTime})_${file.name}`);
                    await uploadBytes(scrimLogRef, file)
                        .then((snapshot) => {
                            console.log('Uploaded ', file.name);
                            hasError = false;
                            this.$toast.info(`${file.name} uploaded.`, {duration:500})
                        })
                        .catch((err) => {
                            console.log('Error occured during uploading.')
                            hasError = true;
                        })
                }
    
                if (!hasError) {
                    endUploadSuccess()
                } else {
                    endUploadFail()
                }
            }
            
        },
    },
    mounted() {
    },
}
</script>